* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  text-decoration: none;
}
footer {
  width: 100%;
  margin-top: auto;
  background: var(--footerbg);
  border-top: 1px solid var(--cardborder);
}
footer .content {
  max-width: 1350px;
  margin: auto;
  padding: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
footer > * {
  font-size: 13px;
}
footer .content p,
footer a {
  color: var(--footertxt);
}

footer .content .box {
  width: 33%;
  /*transition: all 0.4s ease;*/
}
footer .content .topic {
  font-size: 18px;
  color: var(--footertxt);
  margin-bottom: 16px;
}
.weather {
  margin-top: 20px;
}
.weather .weatherheading {
  font-size: 18px;
  margin-bottom: 5px;
}
footer .content p {
  text-align: left;
}
footer .content .lower .topic {
  margin: 24px 0 5px 0;
}
footer .content .lower i {
  padding-right: 16px;
}
footer .content .middle {
  padding-left: 80px;
}
footer .content .middle a {
  line-height: 32px;
}

footer .content .media-icons a {
  font-size: 16px;
  height: 45px;
  width: 45px;
  display: inline-block;
  text-align: center;
  line-height: 43px;
  border-radius: 5px;
  border: 2px solid #222222;
  margin: 30px 5px 0 0;
}
/*ahah */

footer .bottom {
  width: 100%;
  text-align: right;
  color: var(--footertxt);
  padding: 0 40px 5px 0;
}
.bottom p {
  color: var(--footertxt);
}
.cncttxt {
  color: var(--footertxt);
}
footer .content .right textarea {
  height: 80px;
  width: 100%;
  outline: none;
  color: var(--textareacolor);
  background: var(--footertxtarea);
  border-radius: 5px;
  padding-left: 10px;
  font-size: 17px;
  border: 2px solid #222222;
  resize: none;
}
footer .content .right button {
  height: 42px;
  width: 100%;
  font-size: 18px;
  color: var(--btntext);
  background: var(--button);
  outline: none;
  border: 0;
  border-radius: 5px;
  letter-spacing: 1px;
  cursor: pointer;
  margin-top: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.disabled {
  cursor: not-allowed;
  background: grey;
}

@media (max-width: 1100px) {
  footer .content .middle {
    padding-left: 50px;
  }
}
@media (max-width: 950px) {
  footer .content .box {
    width: 50%;
  }
  .content .right {
    margin-top: 40px;
  }
  footer {
    padding-bottom: 50px;
  }
}
@media (max-width: 560px) {
  footer {
    position: relative;
  }
  footer .content .box {
    width: 100%;
    margin-top: 30px;
  }
  footer .content .middle {
    padding-left: 0;
  }

  footer .bottom {
    text-align: left;
    padding: 0 40px 5px 25px;
  }
}

